          proFinalPayRate: '65.1',
          expFinalPayRate: '63.9',
          proMarketCostRate: '16.8',
          expMarketCostRate: '69.9',
          proOperationCostRate: '99.9',
          expOperationCostRate: '100.0'
        },
        {
          bizSector: '家用车-新车',
          insuranceType: '交商合计',
          proPremiumScale: '450',
          proFinalPayRate: '65.1',
          expFinalPayRate: '63.9',
          proMarketCostRate: '16.8',
          expMarketCostRate: '69.9',
          proOperationCostRate: '99.9',
          expOperationCostRate: '100.0'
        },
        {
          bizSector: '家用车-旧车',
          insuranceType: '交强险合计',
          proPremiumScale: '225',
          proFinalPayRate: '65.1',
          expFinalPayRate: '63.9',
          proMarketCostRate: '16.8',
          expMarketCostRate: '69.9',
          proOperationCostRate: '99.9',
          expOperationCostRate: '100.0'
        },
        {
          bizSector: '家用车-旧车',
          insuranceType: '商业险合计',
          proPremiumScale: '225',
          proFinalPayRate: '65.1',
          expFinalPayRate: '63.9',
          proMarketCostRate: '16.8',
          expMarketCostRate: '69.9',
          proOperationCostRate: '99.9',
          expOperationCostRate: '100.0'
        },
        {
          bizSector: '家用车-旧车',
          insuranceType: '交商合计',
          proPremiumScale: '450',
          proFinalPayRate: '65.1',
          expFinalPayRate: '63.9',
          proMarketCostRate: '16.8',
          expMarketCostRate: '69.9',
          proOperationCostRate: '99.9',
          expOperationCostRate: '100.0'
        }
      ]
    }
  },
  computed: {
    currentData () {
      const pageData = []
      const start = (this.currentPage - 1) * this.pageSize
      let end = this.currentPage * this.pageSize
      end = end <= this.data.length ? this.currentPage * this.pageSize : this.data.length
      for (let i = start; i < end; ++i) {
        pageData.push(this.data[i])
      }
      return pageData
    },
    ...mapState({tableHeader: state => state.app.tableHeader})
  },
  methods: {
    spanRow ({ row, column, rowIndex, columnIndex }) {
      if (columnIndex === 0) {
        if (rowIndex === 0) {
          return [this.yearData.length, 1]
        } else {
          return [0, 0]
        }
      }
    },
    spanDetailRow ({ row, column, rowIndex, columnIndex }) {
      if (columnIndex === 0) {
        if (rowIndex === 0 || rowIndex === 3) {
          return [3, 1]
        } else {
          return [0, 0]
        }
      }
    }
  }
}
</script>

